.sitecontainer {
    width: 100%;
    height: 100%;
    margin: 0px;
}

a, abbr, acronym, address, applet, b, big, blockquote, body, button, caption, center, cite, code, dd, del, dfn, div, dl, dt, em, fieldset, font, footer, form, h1, h2, h3, h4, h5, h6, header, html, i, iframe, img, ins, kbd, label, legend, li, nav, object, ol, p, pre, q, s, samp, section, small, span, strike, strong, sub, sup, table, tbody, td, tfoot, th, thead, title, tr, tt, u, ul, var 
{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: baseline;
    background: 0 0;
}
h1 {
    text-align: center;
}
.container {
    padding: 0;
    overflow-x: hidden;
    margin: 0;
    width: 100%;
}

.container-fluid {
    padding: 0;
    overflow-x: hidden;
}

.row {
    margin: 0;
    background-color: transparent;
    width: 100%;
}

a:link {
    color: #FFFFFF;
    -webkit-transition: color 0.4s ease 0s;
    -o-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
}

/* visited link */
a:visited {
    color: #FFFFFF;
    -webkit-transition: color 0.4s ease 0s;
    -o-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
}

/* mouse over link */
a:hover {
    color: #DEFF4F;
    -webkit-transition: color 0.4s ease 0s;
    -o-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
}

/* selected link */
a:active {
    color: #DEFF4F;
    -webkit-transition: color 0.4s ease 0s;
    -o-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
}

.jumbotron {
    background: transparent;
    width: 100%;
}


