
.headerTel {
    background: #DEFF4F;
    font: normal normal normal 17px/1.4em lulo-clean-w01-one-bold,sans-serif;
    padding: 15px;
    padding: 15px;
    margin: 0;
}
.navbar-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.headerTel span {
    color: rgba(15, 147, 254, 1);
}
.companyLabel {
    background-color: rgba(15, 147, 254, 1);
}
.companyLabel span, .navbar-default .navbar-nav > li > a {
    font: normal normal normal 17px/1.4em lulo-clean-w01-one-bold,sans-serif;
    -webkit-transition: color 0.4s ease 0s;
    -o-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
    color: #FFFFFF;
    white-space: nowrap;
    font-weight: bolder;
}
.companyLabel span:hover, 
.navbar-default .navbar-nav > li > a:hover, 
.navbar-default .navbar-nav>.active>a, 
.navbar-default .navbar-nav > .active > a:focus
{
    color: #DEFF4F;
    background-color: transparent;    
    -webkit-transition: color 0.4s ease 0s;    
    -o-transition: color 0.4s ease 0s;    
    transition: color 0.4s ease 0s;
}

.companyLabel img {
    width: 42px; 
    height: 48px; 
    -o-object-fit: fill; 
       object-fit: fill;
}

.navbar {
    margin: 0;
    border: 0;
}

.navbar-default .navbar-nav>.active>a, 
.navbar-default .navbar-nav>.active>a:focus, 
.navbar-default .navbar-nav>.active>a:hover{
    background-color: transparent;
    color: #DEFF4F;
}


